export default function loadFont(fontName, woffUrl, woff2Url) {
  if (typeof window === "undefined") return;
  var nua = window.navigator.userAgent;
  var noSupport = !window.addEventListener || (nua.match(/(Android (2|3|4.0|4.1|4.2|4.3))|(Opera (Mini|Mobi))/) && !nua.match(/Chrome/))
  if (noSupport) {return}
  var loSto = {};
  try {loSto = localStorage || {};} catch (ex) {}
  var localStoragePrefix = "x-font-" + fontName;
  var localStorageUrlKey = localStoragePrefix + "url";
  var localStorageCssKey = localStoragePrefix + "css";
  var storedFontUrl = loSto[localStorageUrlKey];
  var storedFontCss = loSto[localStorageCssKey];
  var styleElement = document.createElement("style");
  styleElement.rel = "stylesheet";
  document.head.appendChild(styleElement);
  if (storedFontCss && (storedFontUrl === woffUrl || storedFontUrl === woff2Url)) {
    styleElement.textContent = storedFontCss;
  } else {
    var url = (woff2Url && supportsWoff2()) ? woff2Url : woffUrl;
    var request = new XMLHttpRequest();
    request.open("GET", url);
    request.onload = function () {
      if (request.status >= 200 && request.status < 400) {
        loSto[localStorageUrlKey] = url;
        loSto[localStorageCssKey] = styleElement.textContent = request.responseText;
      }
    };
    request.send();
  }
}
function supportsWoff2(){
	if(!("FontFace" in window)) return false;
	var f = new FontFace('t', 'url( "data:application/font-woff2;base64," ) format( "woff2" )', {});
	f.load()['catch'](function() {});
	return f.status == 'loading' || f.status == 'loaded';
}